<template>
  <div id="mileage" class="app-container" v-loading="listLoading">
    <!-- 切换面板 -->
    <div class="filter-container">
      <!-- <el-radio-group v-model="changeChoose" class="l-tab">
        <el-radio :label="0">大队查询</el-radio>
        <el-radio :label="1">精确查询</el-radio>
      </el-radio-group>-->
      <div class="radioBtn" :class="{'hasColor':(changeChoose=='0')}" @click="changeChoose='0'">大队查询</div>
      <div class="radioBtn" :class="{'hasColor':(changeChoose=='1')}" @click="changeChoose='1'">精确查询</div>

      <!-- 大队查询查找条件 -->
      <el-form
        :model="LruleForm"
        :rules="Lrules"
        ref="LruleForm"
        class="filterTool"
        v-show="changeChoose==0"
      >
        <el-form-item prop="teamId">
          <el-select v-model="LruleForm.teamId" placeholder="请选择大队">
            <el-option
              v-for="item in LselectData"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item prop="year">
          <el-date-picker
            v-model="LruleForm.year"
            type="year"
            value-format="yyyy"
            class="l-year"
            :clearable="false"
            prefix-icon="el-icon-arrow-down"
            placeholder="请选择年"
          ></el-date-picker>
        </el-form-item>

        <el-form-item prop="month">
          <el-select v-model="LruleForm.month" placeholder="请选择月">
            <el-option
              v-for="item in LselectMonth"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-button type="primary" @click="LsearchList" icon="el-icon-search">搜索</el-button>
      </el-form>
      <!-- 精确查找条件 -->
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        class="filterTool"
        v-show="changeChoose==1"
      >
        <el-form-item prop="imeiNo">
          <el-cascader
            v-model="ruleForm.imeiNo"
            placeholder="选择设备"
            :props="{expandTrigger:'hover'}"
            :options="dataCar"
            :show-all-levels="false"
            clearable
            filterable
          >
            <template slot-scope="{ node, data }">
              <span>{{ data.label }}</span>
              <span v-if="!node.isLeaf">({{ data.children.length }})</span>
            </template>
          </el-cascader>
        </el-form-item>
        <el-form-item prop="value1">
          <el-date-picker
            v-model="ruleForm.value1"
            type="date"
            placeholder="选择日期"
            value-format="timestamp"
            :picker-options="pickerOptions"
          ></el-date-picker>
        </el-form-item>
        <span class="divider">-</span>
        <el-form-item prop="value2">
          <el-date-picker
            v-model="ruleForm.value2"
            type="date"
            placeholder="选择日期"
            value-format="timestamp"
            :picker-options="pickerOptions"
          ></el-date-picker>
        </el-form-item>
        <el-button type="primary" icon="el-icon-search" @click="searchList">搜索</el-button>
        <!-- <el-button type="primary" icon="el-icon-search" @click="searchAll">搜索所有设备</el-button> -->
      </el-form>
    </div>

    <!-- 大队查询 -->
    <div class="cont" v-show="changeChoose==0">
      <div class="resDiv">
        <div class="myTable tableBox">
          <!-- 表格 -->
          <el-table
            v-show="Llist.length>0"
            @sort-change="sortLF"
            :data="Llist"
            border
            highlight-current-row
            style="width: 99%;"
            :row-class-name="tableRowClassName"
          >
            <el-table-column label="序号" align="center" width="80" prop="lIndex"></el-table-column>
            <el-table-column label="设备号" prop="deviceNo" align="center" min-width="100"></el-table-column>
            <el-table-column label="车牌号" prop="licenseNo" align="center" min-width="100"></el-table-column>
            <!-- 在这里处理数据，方便排序 -->
            <el-table-column
              label="行驶里程"
              min-width="200px"
              align="center"
              sortable="custom"
              prop="mile"
            >
              <template slot-scope="scope">
                <span v-if="scope.row.mile<1000">{{ scope.row.mile }} m</span>
                <span v-else>{{ (scope.row.mile/1000).toFixed(2) }} Km</span>
              </template>
            </el-table-column>
            <el-table-column
              label="行驶时长"
              min-width="200px"
              align="center"
              sortable="custom"
              prop="runTime"
            >
              <template slot-scope="scope">
                <span v-if="scope.row.runTime<60">{{ scope.row.runTime }}秒</span>
                <span
                  v-else-if="scope.row.runTime<3600"
                >{{ Math.floor(scope.row.runTime/60) }}分{{ scope.row.runTime%60 }}秒</span>
                <span
                  v-else
                >{{ Math.floor(scope.row.runTime/3600) }}小时{{ Math.floor(scope.row.runTime%3600/60) }}分{{ scope.row.runTime%3600%60 }}秒</span>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div id="Lcharts" v-show="Llist.length>0" class="echarts"></div>
      </div>
    </div>

    <!-- 精确查询 -->
    <div class="cont" v-show="changeChoose==1">
      <div class="resDiv">
        <div class="myTable tableBox">
          <!-- 表格 -->
          <el-table
            v-show="list.length>0"
            @sort-change="sortF"
            :data="list"
            :summary-method="getSummaries"
            show-summary
            fit
            border
            highlight-current-row
            style="width: 99%;"
            :row-class-name="tableRowClassName"
          >
            <el-table-column label="序号" align="center" type="index" width="80" :index="myIndex"></el-table-column>
            <el-table-column label="设备号" prop="deviceNo" align="center" min-width="100"></el-table-column>
            <el-table-column
              label="行驶里程"
              min-width="200px"
              align="center"
              sortable="custom"
              prop="mile"
            >
              <template slot-scope="scope">
                <!-- <span>{{ (scope.row.mile==0?0:(scope.row.mile/1000).toFixed(2)) }} Km</span> -->
                <span v-if="scope.row.mile<1000">{{ scope.row.mile }} m</span>
                <span v-else>{{ (scope.row.mile/1000).toFixed(2) }} Km</span>
              </template>
            </el-table-column>
            <el-table-column
              label="行驶时长"
              min-width="200px"
              align="center"
              sortable="custom"
              prop="runTime"
            >
              <template slot-scope="scope">
                <span v-if="scope.row.runTime<60">{{ scope.row.runTime }}秒</span>
                <span
                  v-else-if="scope.row.runTime<3600"
                >{{ Math.floor(scope.row.runTime/60) }}分{{ scope.row.runTime%60 }}秒</span>
                <span
                  v-else
                >{{ Math.floor(scope.row.runTime/3600) }}小时{{ Math.floor(scope.row.runTime%3600/60) }}分{{ scope.row.runTime%3600%60 }}秒</span>
              </template>
            </el-table-column>
            <el-table-column label="日期" prop="backTime" align="center" min-width="100"></el-table-column>
          </el-table>
          <!-- 分页 -->
          <pagination
            v-show="total>0"
            :total="total"
            :page.sync="listQuery.page"
            :limit.sync="listQuery.limit"
            @pagination="changePage"
          />
        </div>
        <div id="charts" v-show="list.length>0" class="echarts"></div>
      </div>
    </div>
  </div>
</template>

<script>
import Pagination from "../../../components/Pagination";
import {
  mileageList,
  milesRangeByTeam
} from "../../../api/managementArchives/routeM"; // 里程、时长接口 milesRangeByTeam按大队
// import { teamAndCarList } from "../../../api/realTimeMonitoring";// 获取设备
import echarts from "echarts";
import chaixieImg from "../../../../static/images/chaixie-circle.png";
import yijianImg from "../../../../static/images/yijian-circle.png";

export default {
  name: "mileage",
  components: { Pagination },
  data() {
    return {
      changeChoose: 0, // 默认显示大队查询面板
      // 大队查询
      LruleForm: {
        teamId: "", // 大队ID
        type: 2, // 1：统计一月的 2统计一年的，此时month传null
        month: 0,
        year: new Date().getFullYear().toString()
      },
      LselectData: [], // 大队下拉框数据,
      Lrules: {
        // 验证规则
        teamId: [{ required: true, message: "请选择大队", trigger: "change" }],
        year: [{ required: true, message: "请选择年", trigger: "change" }]
      },
      LselectMonth: [
        { value: 0, label: "全年" },
        { value: 1, label: "一月" },
        { value: 2, label: "二月" },
        { value: 3, label: "三月" },
        { value: 4, label: "四月" },
        { value: 5, label: "五月" },
        { value: 6, label: "六月" },
        { value: 7, label: "七月" },
        { value: 8, label: "八月" },
        { value: 9, label: "九月" },
        { value: 10, label: "十月" },
        { value: 11, label: "十一月" },
        { value: 12, label: "十二月" }
      ],
      Llist: [], // 大队搜索结果
      Id2ser: {}, // 车牌号对应设备号
      myLChart: undefined,
      //./ 大队查询
      myChart: undefined,
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now() - 24 * 60 * 60 * 1000; // 今天也不能选，因为今天的数据还未统计完全
        }
      },
      allData: [], // 未分页前的所有数据
      dataCar: [], // 设备数据
      ruleForm: {
        imeiNo: [], // 设备号 级联选择器是一个数组
        value1: "", // 开始或者结束时间
        value2: ""
      },
      rules: {
        imeiNo: [{ required: true, message: "请选择设备", trigger: "change" }],
        value1: [
          {
            type: "date",
            required: true,
            message: "请选择起止日期",
            trigger: "change"
          }
        ],
        value2: [
          {
            type: "date",
            required: true,
            message: "请选择起止日期",
            trigger: "change"
          }
        ]
      },
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      list: [], // 搜索结果
      total: 0,
      indexMethod: 1, // 序号
      listLoading: false,
      listQuery: {
        page: 1,
        limit: 10
      },
      allSerialNo: [], // 所有设备号数组
      showMyBtn: sessionStorage.mybtn // 显示测试按钮
    };
  },
  created() {
    let _t = this,
      teamCarListData = JSON.parse(sessionStorage.teamCarList);
    _t.dataCar = _t.formatCas(teamCarListData, "serialNo", "licenseNo"); // 设置车下拉树的数据格式

    // 图表初始化
    _t.$nextTick(() => {
      _t.myChart = echarts.init(document.getElementById("charts"));
      _t.myLChart = echarts.init(document.getElementById("Lcharts"));
      let options = {
        color: ["#3C7EFE", "#FA751A"],
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow"
          },
          formatter: function(params) {
            // 格式化数据
            let text = "",
              time = "";
            params.forEach(x => {
              // console.log('x',x);
              if (x.seriesType == "bar") {
                // 只显示一种类型的tip
                time = x.axisValue;
                let cont = ""; // 处理数据
                if (x.seriesName == "行驶里程") {
                  if (x.data < 1000) {
                    cont = x.data + "m";
                  } else {
                    cont = (x.data / 1000).toFixed(2) + "Km";
                  }
                } else if (x.seriesName == "行驶时长") {
                  if (x.data < 60) {
                    cont = x.data + "秒";
                  } else if (x.data < 3600) {
                    cont =
                      Math.floor(x.data / 60) + "分" + (x.data % 60) + "秒";
                  } else {
                    cont =
                      Math.floor(x.data / 3600) +
                      "小时" +
                      Math.floor((x.data % 3600) / 60) +
                      "分" +
                      ((x.data % 3600) % 60) +
                      "秒";
                  }
                }
                text += `${x.marker}${x.seriesName}:${cont}<br>`;
              }
            });
            return `${time}<br>${text}`;
          }
        },
        grid: {
          bottom: 75
        },
        dataZoom: [{ type: "inside" }, { type: "slider" }],
        legend: {
          data: ["行驶里程", "行驶时长"]
          // data: [
          //   { name: "行驶里程", icon: "image://" + chaixieImg },

          //   { name: "行驶时长", icon: "image://" + yijianImg }

          // ]
        },
        xAxis: {
          data: [],
          axisLabel: {
            formatter: function(value) {
              return value.replace("-", "\n");
            }
          }
        },
        yAxis: [
          {
            type: "value",
            name: "行驶里程(Km)",
            axisLabel: {
              formatter: function(value) {
                return (value / 1000).toFixed(2) * 1;
              }
            }
          },
          {
            type: "value",
            name: "行驶时长(h)",
            axisLabel: {
              formatter: function(value) {
                return Math.ceil(value / 3600);
              }
            }
          }
        ],
        series: []
      }; // 样式
      _t.myChart.setOption(options);
      _t.myLChart.setOption(options);
    });

    // 处理大队查询数据下拉框
    for (let i in teamCarListData) {
      let teamId = teamCarListData[i][0].teamId; // teamId返回在对象的数组中
      _t.LselectData.push({ value: teamId, label: i == "" ? "未分组" : i });
    }
  },
  methods: {
    formatCas(data, imei, name) {
      // 格式化级联选择器数据 data是原始数据 imei是vaue值 name是label值
      let result = [],
        id = 0,
        _t = this;
      _t.allSerialNo = []; // 所有设备号数组
      for (let i in data) {
        let childs = []; // 格式化子数组
        data[i].forEach(x => {
          childs.push({ value: x[imei], label: x[name], disabled: false });
          _t.allSerialNo.push(x.serialNo); // 所有设备号数组
          // 设置车牌号对应设备号
          _t.Id2ser[x.serialNo] = x.licenseNo;
        });
        result.push({
          value: `i${id++}`,
          label: i == "" ? "未分组" : i,
          children: childs
        });
      }
      return result;
    },
    getList(item) {
      // 获取搜索结果数据
      let _t = this;
      if (item.length > 31) {
        this.$message.warning("请搜素31天内的数据,请重新选择!");
        return;
      }
      _t.listLoading = true;
      mileageList({
        // carImei: _t.ruleForm.imeiNo[1],
        // dates: item
        deviceNo: _t.ruleForm.imeiNo[1],
        dates: item,
        startYear: Number(item[0].split("-")[0]),
        startMonth: Number(item[0].split("-")[1]),
        startDay: Number(item[0].split("-")[2]),
        endYear: Number(item[item.length - 1].split("-")[0]),
        endMonth: Number(item[item.length - 1].split("-")[1]),
        endDay: Number(item[item.length - 1].split("-")[2])
      })
        .then(res => {
          if (res.success) {
            _t.total = 0; // 查询重置
            _t.allData = []; // 清空数据
            for (let i in res.data) {
              res.data[i].backTime = i; // 时间
              res.data[i].sortTime = Date.parse(i); // 排序时间
              _t.allData.push(res.data[i]); // 赋值
              res.data[i].mile = res.data[i].mile.toFixed(2)*1; // 处理里程小数点
            }
            _t.allData.sort(function(a, b) {
              // 默认按时间排序
              return a.sortTime - b.sortTime;
            });
            console.log('fweaf',_t.allData)
            _t.total = _t.allData.length; // 总数
            // 分页
            _t.changePage();
          }
        })
        .finally(() => {
          _t.listLoading = false;
        });
    },
    searchList() {
      // 查找数据
      let _t = this;
      _t.$refs.ruleForm.validate(valid => {
        if (valid) {
          _t.getList(_t.dealDate());
        }
      });
    },
    dealDate() {
      // 处理时间数组
      let _t = this;
      function formatTime(time) {
        // 格式时间
        let d = new Date(time),
          y = d.getFullYear(),
          m = d.getMonth() + 1,
          day = d.getDate();
        m = m < 10 ? "0" + m : m;
        day = day < 10 ? "0" + day : day;
        return `${y}-${m}-${day}`;
      }
      let oneDay = 1000 * 60 * 60 * 24, // 一天的毫秒数
        s = Math.min(_t.ruleForm.value1, _t.ruleForm.value2), // 取小的值作为开始时间
        e = Math.max(_t.ruleForm.value1, _t.ruleForm.value2) + oneDay - 1, // 大的值作为结束时间并且加上一天的毫秒
        len = Math.ceil((e - s) / oneDay), // 用于将日期遍历
        dayArr = []; // 传到后台的日期数组
      for (let i = 0; i < len; i++) {
        let time = formatTime(s + oneDay * i);
        dayArr.push(time);
      }
      return dayArr;
    },
    changePage() {
      // 分页
      let _t = this;
      let s = (_t.listQuery.page - 1) * _t.listQuery.limit,
        e = s + _t.listQuery.limit;
      _t.list = _t.allData.slice(s, e);
    },
    myIndex(index) {
      // 序号
      return index + (this.listQuery.page - 1) * this.listQuery.limit + 1;
    },
    sortF(e) {
      // 排序
      let _t = this;
      if (e.order == "ascending") {
        // 升序
        _t.allData.sort(function(a, b) {
          return a[e.prop] - b[e.prop];
        });
      } else if (e.order == "descending") {
        // 降序
        _t.allData.sort(function(a, b) {
          return b[e.prop] - a[e.prop];
        });
      } else {
        _t.allData.sort(function(a, b) {
          // 默认按时间排序
          return a.sortTime - b.sortTime;
        });
      }
      _t.changePage();
    },
    searchAll() {
      // 查找所有设备
      // 正式服从几月开始呢
      let _t = this,
        index = 0, // 设备号遍历次数
        arr = [..._t.allSerialNo], // 设备号数组
        times = 0, // 时间遍历次数
        timeArr = _t.dealDate(); // 时间数组
      // 需要遍历日期，到最后一天时，从头开始
      function gets() {
        console.log(arr, timeArr);
        console.log("第" + index + "个设备", "请求到第" + times + "天");
        console.log(arr[index], [timeArr[times]]);
        mileageList({
          // carImei: arr[index],
          // dates: [timeArr[times]]
          deviceNo: arr[index],
          dates: [timeArr[times]],
          startYear: Number(timeArr[times].split("-")[0]),
          startMonth: Number(timeArr[times].split("-")[1]),
          startDay: Number(timeArr[times].split("-")[2]),
          endYear: Number(timeArr[times].split("-")[0]),
          endMonth: Number(timeArr[times].split("-")[1]),
          endDay: Number(timeArr[times].split("-")[2])
        })
          .then(res => {
            if (res.success) {
              if (++times >= timeArr.length) {
                // 遍历到最后一天了,设备号到下一位
                times = 0; // 重置天数
                if (++index >= arr.length) {
                  index = 0; // 重置设备下标
                }
              }
            }
          })
          .finally(() => {
            gets();
          });
      }
      gets();
    },
    getSummaries(param) {
      // 合计
      let _t = this,
        nums = [],
        miles = 0,
        times = 0;
      _t.allData.forEach(x => {
        miles += x.mile; // 行驶里程
        times += x.runTime; // 行驶时长
      });
      // 处理里程数据
      if (miles < 1000) {
        miles = miles + " m";
      } else {
        miles = (miles / 1000).toFixed(2) + " Km";
      }
      // 处理时长数据
      if (times < 60) {
        times = times + "秒";
      } else if (times < 3600) {
        times = Math.floor(times / 60) + "分" + (times % 60) + "秒";
      } else {
        times =
          Math.floor(times / 3600) +
          "小时" +
          Math.floor((times % 3600) / 60) +
          "分" +
          ((times % 3600) % 60) +
          "秒";
      }
      nums[0] = "合计";
      nums[2] = miles; // 也可用findIndex来获取下标但没必要
      nums[3] = times; // param.columns.findIndex(x=>x.property=="runTime")
      return nums;
    },
    LsearchList() {
      // 获取大队的数据
      // 查找数据
      let _t = this;
      _t.$refs.LruleForm.validate(valid => {
        if (valid) {
          _t.listLoading = true;
          // console.warn(_t.LruleForm);
          milesRangeByTeam({
            teamId: _t.LruleForm.teamId,
            year: _t.LruleForm.year * 1,
            type: _t.LruleForm.month == 0 ? 2 : 1, // 1：统计一月的 2统计一年的，此时month传null
            month: _t.LruleForm.month
          })
            .then(res => {
              // console.warn(res)
              if (res.success) {
                _t.Llist = []; // 清空数据
                let lIndex = 1;
                for (let i in res.data) {
                  // 重组数据
                  let obj = {
                    lIndex: lIndex++,
                    deviceNo: i,
                    licenseNo: _t.Id2ser[i],
                    mile: 0,
                    runTime: 0
                  };
                  res.data[i].forEach(x => {
                    // 将里程时长叠加返回
                    obj.mile += x.mile;
                    obj.runTime += x.runTime;
                  });
                  obj.mile = obj.mile.toFixed(2)*1; // 处理里程小数点
                  _t.Llist.push(obj);
                }
                console.log(_t.Llist);
              }
            })
            .finally(() => {
              _t.listLoading = false;
            });
        }
      });
    },
    sortLF(e) {
      // 大队排序
      // 排序
      let _t = this;
      if (e.order == "ascending") {
        // 升序
        _t.Llist.sort(function(a, b) {
          return a[e.prop] - b[e.prop];
        });
      } else if (e.order == "descending") {
        // 降序
        _t.Llist.sort(function(a, b) {
          return b[e.prop] - a[e.prop];
        });
      } else {
        _t.Llist.sort(function(a, b) {
          // 默认按时间排序
          return a.lIndex - b.lIndex;
        });
      }
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    }
  },
  watch: {
    allData: function(val, old) {
      let _t = this;
      // 图表
      let myChartData = { xAxis: [], mile: [], runTime: [] }; // 图表数据
      val.forEach(x => {
        // 赋值
        myChartData.xAxis.push(x.backTime); // 横坐标时间
        myChartData.mile.push(x.mile); // 纵坐标行驶里程
        myChartData.runTime.push(x.runTime); // 纵坐标行驶时长
      });
      _t.myChart.setOption({
        xAxis: {
          data: myChartData.xAxis
        },
        series: [
          {
            name: "行驶里程",
            type: "bar",
            barMaxWidth: 20,
            data: myChartData.mile
          },
          {
            name: "行驶时长",
            type: "bar",
            yAxisIndex: 1,
            barMaxWidth: 20,
            barGap: 0,
            data: myChartData.runTime
          },
          {
            name: "行驶里程",
            type: "line",
            smooth: true,
            data: myChartData.mile
          },
          {
            name: "行驶时长",
            type: "line",
            smooth: true,
            yAxisIndex: 1,
            data: myChartData.runTime
          }
        ]
      });
    },
    Llist: function(val, old) {
      // 大队查询
      let _t = this;
      // 图表
      let myChartData = { xAxis: [], mile: [], runTime: [] }; // 图表数据
      val.forEach(x => {
        // 赋值
        myChartData.xAxis.push(x.licenseNo); // 横坐标
        myChartData.mile.push(x.mile); // 纵坐标行驶里程
        myChartData.runTime.push(x.runTime); // 纵坐标行驶时长
      });
      _t.myLChart.setOption({
        xAxis: {
          data: myChartData.xAxis
        },
        series: [
          {
            name: "行驶里程",
            type: "bar",
            barMaxWidth: 20,
            data: myChartData.mile
          },
          {
            name: "行驶时长",
            type: "bar",
            yAxisIndex: 1,
            barMaxWidth: 20,
            barGap: 0,
            data: myChartData.runTime
          },
          {
            name: "行驶里程",
            type: "line",
            smooth: true,
            data: myChartData.mile
          },
          {
            name: "行驶时长",
            type: "line",
            smooth: true,
            yAxisIndex: 1,
            data: myChartData.runTime
          }
        ]
      });
    }
  }
};
</script>
