import request from '../../request'
//路线列表
export function roadlist(data) {
  return request({
    url: '/map/roadlist',
    method: 'post',
    data
  })
}
//路线删除
export function deletelist(data) {
  return request({
    url: '/map/delete',
    method: 'post',
    data
  })
}
// 路线查看
export function viewlist(data) {
  return request({
    url: '/map/complexGeometry',
    method: 'post',
    data
  })
}
// 车路绑定列表
export function carRoadList(data) {
  return request({
    url: '/planRoad/list',
    method: 'post',
    data
  })
}
// 车路绑定提交
export function carRoadBind(data) {
  return request({
    url: '/planRoad/add',
    method: 'post',
    data
  })
}
// 车路绑定删除
export function carRoadUnbind(data) {
  return request({
    url: '/planRoad/remove',
    method: 'post',
    data
  })
}
