// 以上接口被改成这个接口（行驶里程 时长）
export function mileageList(data) {
  return request({
    url: '/statistics/milesrange',
    method: 'post',
    data,
    headers:{'content-type':'application/json'}
  })
}
// 大队里程、时长统计
export function milesRangeByTeam(data) {
  return request({
    url: '/statistics/milesRangeByTeam',
    method: 'post',
    data
  })
}
//根据路线id查询车组
export function queryBindedGroups(data) {
  return request({
    url: '/eoms/groups/queryBindedGroupsByRoute',
    method: 'post',
    data
  })
}

//查询车组
export function groupSelect(data) {
  return request({
    url: '/eoms/groups/queryAllGroup',
    method: 'post',
    data
  })
}
//路线绑定车组
export function bindGroups(data) {
  return request({
    url: '/eoms/groups/bindGroup',
    method: 'post',
    data
  })
}

